:root {
  --font-body: "Manrope", sans-serif;
}

:root {
  --black: hsl(0, 0%, 0%);
  --softcyan: hsl(174, 77%, 80%);
  --strongcyan: hsl(174, 86%, 45%);
  --lightgrayishred: hsl(14, 92%, 95%);
  --lightred: hsl(15, 100%, 70%);
  --paleblue: hsl(226, 100%, 87%);
  --white: hsl(0, 0%, 100%);
  --verypaleblue: hsl(230, 100%, 99%);
  --lightgrayishblue: hsl(224, 65%, 95%);
  --lightgrayishblue2: hsl(223, 50%, 87%);
  --grayishblue: hsl(225, 20%, 60%);
  --darkdesaturatedblue: hsl(227, 35%, 25%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}
html:focus-within {
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
  padding: 0;
}

body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
  font-family: var(--font-body);
}

img,
picture {
  max-width: 100%;
  display: block;
}

input,
button,
textarea,
select {
  font: inherit;
}

@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
}

ul,
ol {
  list-style: none;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
  max-width: 90rem;
  margin: 0 auto;
  background-image: url("../app/media/images/bg-pattern.svg");
  background-color: var(--verypaleblue);
  background-position: top;
  background-repeat: no-repeat;
  background-size: contain;
  position: relative;
}
@media (max-width: 599px) {
  .container {
    background-position: left top;
    background-size: initial;
  }
}
.container h1 {
  margin-top: 6rem;
  text-align: center;
  font-size: 1.6rem;
}
.container h2 {
  margin: 0.5rem 0 5rem 0;
  font-size: 0.8rem;
  text-align: center;
  color: var(--grayishblue);
}
@media (max-width: 599px) {
  .container h2 {
    font-size: 1rem;
    width: 240px;
  }
}

footer {
  font-size: 11px;
  text-align: center;
}
footer:focus-within {
  outline: 3px dotted green;
}

footer a {
  color: #3e52a3;
}
footer a:focus-within {
  outline: 3px dotted green;
}

.pricing-circles img {
  position: absolute;
  top: 60px;
  left: 50%;
  transform: translateX(-50%);
}

.pricing-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 540px;
  width: 100%;
  height: 400px;
  padding: 2rem 3rem;
  margin: 0 auto;
  background-color: var(--white);
  border-radius: 10px;
  box-shadow: 10px 10px 20px var(--lightgrayishblue);
  z-index: 10;
  position: relative;
}
@media (max-width: 599px) {
  .pricing-container {
    width: 375px;
    height: 560px;
    padding: 2rem 1rem;
  }
}

.pricing-stats {
  display: flex;
  align-items: center;
  width: 100%;
  margin-top: 0.5rem;
  font-weight: 800;
  font-size: 0.9rem;
  letter-spacing: 1px;
  color: var(--grayishblue);
}
@media (max-width: 599px) {
  .pricing-stats {
    flex-direction: column;
    justify-content: center;
  }
}

.pricing-slider {
  width: 100%;
}
@media (max-width: 599px) {
  .pricing-slider {
    position: absolute;
    margin: 2rem 0;
  }
}

.pricing-value {
  width: max-content;
  font-size: 2.3rem;
  font-weight: 800;
  color: var(--darkdesaturatedblue);
  display: flex;
  justify-content: flex-end;
  margin-left: 6.5rem;
  position: relative;
}
@media (max-width: 599px) {
  .pricing-value {
    position: absolute;
    top: 145px;
    left: 50%;
    transform: translateX(-75%);
  }
}
.pricing-value::after {
  content: "/ month";
  font-weight: 800;
  font-size: 0.9rem;
  letter-spacing: 1px;
  color: var(--grayishblue);
  position: absolute;
  top: 50%;
  right: -76px;
  transform: translateY(-50%);
}
@media (max-width: 599px) {
  .pricing-value {
    margin: 0;
  }
  .pricing-value::after {
    content: "/ month";
    font-weight: 800;
    font-size: 0.9rem;
    letter-spacing: 1px;
    color: var(--grayishblue);
    position: absolute;
    top: 50%;
    right: -76px;
    transform: translateY(-50%);
  }
}

#pricing-slider {
  -webkit-appearance: none;
  width: 100%;
  height: 50px;
  background: transparent;
  margin-top: 1rem;
  cursor: grab;
}
@media (max-width: 599px) {
  #pricing-slider {
    width: 85%;
    display: block;
    margin: 1rem auto;
  }
}
#pricing-slider::-webkit-slider-thumb {
  -webkit-appearance: none;
}
#pricing-slider:focus-within {
  outline: 3px dotted green;
}
#pricing-slider::-ms-track {
  width: 100%;
  cursor: grab;
  background: transparent;
  border-color: transparent;
  color: transparent;
}
#pricing-slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  border: 1px solid var(--lightgrayishblue);
  height: 40px;
  width: 40px;
  border-radius: 50%;
  background: var(--softcyan);
  background-image: url(../../../app/media/images/icon-slider.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 50%;
  cursor: grab;
  margin-top: -14px;
  /* You need to specify a margin in Chrome, but in Firefox and IE it is automatic */
}
#pricing-slider::-webkit-slider-thumb:active {
  cursor: grabbing;
  background: var(--strongcyan);
  background-image: url(../../../app/media/images/icon-slider.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 50%;
}
#pricing-slider::-moz-range-thumb {
  box-shadow: 0 10px 10px var(--softcyan);
  width: 20px;
  height: 20px;
  scale: 2;
  background: var(--strongcyan);
  border-radius: 50%;
  border: 1px solid var(--lightgrayishblue);
  background-image: url(../../../app/media/images/icon-slider.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 50%;
}
#pricing-slider::-moz-range-thumb:hover {
  background: var(--softcyan);
  background-image: url(../app/media/images/icon-slider.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 50%;
}
#pricing-slider::-moz-range-thumb:active {
  cursor: grabbing;
  background: var(--strongcyan);
  background-image: url(../app/media/images/icon-slider.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 50%;
}
#pricing-slider::-webkit-slider-runnable-track {
  width: 100%;
  height: 8px;
  cursor: pointer;
  background: var(--lightgrayishblue);
  border-radius: 25px;
  border: none;
}
#pricing-slider:focus::-webkit-slider-runnable-track {
  border: 2px dotted green;
}
#pricing-slider::-moz-range-track {
  background-color: var(--lightgrayishblue);
  height: 8px;
  border-radius: 25px;
  width: 100%;
  cursor: pointer;
  border: none;
}
#pricing-slider::-moz-range-progress {
  background: linear-gradient(to right, var(--strongcyan), var(--softcyan));
}
#pricing-slider::-ms-track {
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
  width: 20px;
  height: 20px;
  scale: 2;
  background: var(--softcyan);
  border-radius: 50%;
  border: 1px solid var(--lightgrayishblue);
}
#pricing-slider::-ms-fill-lower {
  background: #2a6495;
  border: 0.2px solid #010101;
  border-radius: 2.6px;
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
}
#pricing-slider::-ms-fill-upper {
  background: #3071a9;
  border: 0.2px solid #010101;
  border-radius: 2.6px;
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
}

.billing {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin-top: 1rem;
  font-size: 12px;
  color: var(--grayishblue);
  position: relative;
}
.billing::after {
  content: "25% discount";
  color: var(--lightred);
  background-color: var(--lightgrayishred);
  border-radius: 25px;
  padding: 0.1rem 0.4rem;
  font-size: 10px;
}
@media (max-width: 599px) {
  .billing::after {
    content: "-25%";
    position: absolute;
    right: 0;
  }
}
@media (max-width: 599px) {
  .billing {
    margin-top: 11rem;
  }
}

.billing-monthly {
  margin: 0 1rem;
}
@media (max-width: 599px) {
  .billing-monthly {
    margin: 0;
  }
}

.billing-yearly {
  margin: 0 1rem;
}
@media (max-width: 599px) {
  .billing-yearly {
    margin: 0;
  }
}

.billing-switch-label {
  position: relative;
  display: inline-block;
  width: 49px;
  height: 25px;
  margin: 0 1rem;
  background-color: var(--lightgrayishblue2);
  border-radius: 25px;
}

.billing-switch {
  opacity: 0;
  width: 0;
  height: 0;
}
.billing-switch:focus + .billing-slider {
  box-shadow: 0 0 1px var(--lightgrayishblue);
  background-color: var(--softcyan);
  border-radius: 25px;
  outline: 3px dotted green;
}
.billing-switch:checked + .billing-slider::before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

.billing-slider {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: transparent;
  transition: 250ms;
  -webkit-transition: 250ms;
  cursor: pointer;
}
.billing-slider::before {
  position: absolute;
  content: "";
  width: 15px;
  height: 15px;
  left: 4px;
  bottom: 4px;
  background-color: var(--white);
  transition: 250ms;
  -webkit-transition: 250ms;
  border-radius: 50%;
}

.divider {
  border-bottom: 1px solid var(--lightgrayishblue);
  margin: 3rem 0 2rem 0;
  width: 540px;
  height: 1px;
  display: block;
}
@media (max-width: 599px) {
  .divider {
    width: 375px;
  }
}

.details-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}
@media (max-width: 599px) {
  .details-container {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}

.details-pricing {
  font-size: 12px;
  color: var(--grayishblue);
}
@media (max-width: 599px) {
  .details-pricing {
    font-size: 14px;
  }
}
.details-pricing > * {
  position: relative;
  margin: 0.5rem 0;
  padding-left: 1.5rem;
}
@media (max-width: 599px) {
  .details-pricing > * {
    position: initial;
    text-align: center;
  }
}
.details-pricing > *::before {
  content: url("../app/media/images/icon-check.svg");
  position: absolute;
  left: 0;
}
@media (max-width: 599px) {
  .details-pricing > *::before {
    position: initial;
    padding-right: 1rem;
  }
}

.details-button button {
  background-color: var(--darkdesaturatedblue);
  color: var(--paleblue);
  padding: 0.8rem 3.5rem;
  border-radius: 33px;
  border: none;
  font-size: 12px;
  cursor: pointer;
}
@media (max-width: 599px) {
  .details-button button {
    margin-top: 2rem;
    font-size: 14px;
  }
}
.details-button button:focus-within {
  outline: 3px dotted green;
  color: var(--white);
}
.details-button button:hover {
  color: var(--white);
}